import React from "react";
import { Link } from "react-router-dom";
import "./Detail.css";
import "../components/Movie.css"



class Detail extends React.Component {
  
    componentDidMount() {
      const { location, history } = this.props;
      if (location.state === undefined) {
        history.push("/");
      }
    }
    render() {
        const { location } = this.props;
       
        if (location.state) {
        //   return <span>{location.state.title}</span>;
          return(<div className="detail" >
              <div className="background"><img src={location.state.medium_cover_image} alt={location.state.title} title={location.state.title} /></div>
              <div className="detail__content">
              <img src={location.state.medium_cover_image} alt={location.state.title} title={location.state.title} />
              <div class="movie">
              <h3 class="movie__title">{location.state.title}</h3>
              <h4>{location.state.rating}/10</h4>
            <h5  class="movie__year">{location.state.runtime} min  &nbsp;| &nbsp; {location.state.year}</h5>
            <ul class="movie__genres">
              {location.state.genres.map((genre, index) => (
                <li key={index} >
                  {genre}
                </li>
              ))}
            </ul>
            <p className="movie__summary">{location.state.summary}</p>
            
            </div>
            <Link to="/" className="detail__link">&#x2716;</Link>
            
              </div>
          </div>);
        } else {
          return null;
        }
      }
    }
export default Detail;